import { Picker, List, WhiteSpace,NavBar, Icon, Switch} from 'antd-mobile';
import { createForm } from 'rc-form';
import React from 'react' 

// 如果不是使用 List.Item 作为 children


const colors = [
  {
    label:
    (<div>     
      <span>小</span>
    </div>),
    value: '小',
  },
  {
    label:
    (<div>
      <span>中</span>
    </div>),
    value: '中',
  },
  {
    label:
    (<div>
      <span>大</span>
    </div>),
    value: '大',
  },
];
class Test extends React.Component {
   state = {
    data: [],
    cols: 1,
    asyncValue: [],
  };
  onChangeColor = (color) => {
    this.setState({
      colorValue: color,
    });
  };


  render() {
    const { getFieldProps } = this.props.form;
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
	      onLeftClick={() => this.props.history.goBack()}  
	    >个人设置</NavBar>
      <WhiteSpace size="lg" />
      <List style={{ backgroundColor: 'white' }} className="picker-list">
        <List.Item
          extra={<Switch
            {...getFieldProps('Switch1', {
              initialValue: this.state.checked1,
              valuePropName: 'checked',
              checked:'true',
              onChange: (val) => {
                
              },
            })}
            onClick={(checked) => {
              // set new value
              this.props.form.setFieldsValue({
                Switch1: checked,
              });
            }}
          />}
        >WIFI下自动播放</List.Item>
        <List.Item
          extra={<Switch
            checked={this.state.checked}
            onChange={() => {
              this.setState({
                checked: !this.state.checked,
              });
            }}
          />}
        >非WIFI网络下不下载图片</List.Item>
       
        <List.Item extra={"1.28M"}>清理缓存</List.Item>
        
        <Picker
          data={colors}
          value={this.state.colorValue}
          cols={1}
          onChange={this.onChangeColor}
        >
          <List.Item arrow="horizontal">正文字号</List.Item>
        </Picker>
        
        
        
      </List>
      <WhiteSpace  />
        
      	<List.Item
      		extra={"版本号V1.0.1"}
      	>当前版本</List.Item>
      	
    </div>);
  }
}

const Setting = createForm()(Test);
export default Setting;