import React,{Component} from 'react';
import {NavBar,Icon} from "antd-mobile"
import Stroe from '../Stroe'
//状态管理方法
class Detail extends Component{
    constructor(props){
        super(props)
        this.state={
            str:Stroe.getState()
        }
        this.changeItem = this.changeItem.bind(this)
    }
    changeItem() {
        this.setState({
            str: Stroe.getState()
        })
    }
    componentDidMount() {
        Stroe.subscribe(this.changeItem)
        console.log(this.state.str)

    }
    componentWillUnmount() {
        // 卸载异步操作设置状态
        clearTimeout(this.timeouter)
        this.setState = (state, callback) => {
            return
        }
    }
    render(){
        return(
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                    rightContent={[
                        <Icon key="1" type="ellipsis" onClick={() => this.props.history.push('/setting')} />,
                    ]}>新闻详情</NavBar>

                {/* <iframe src={"'" + this.state.str + "'"} style={{ border: 0, width: "100%", height:'700px' }}/> */}
            </div>
        )
    }

}

