import {  NavBar, Icon } from 'antd-mobile';
import { createForm } from 'rc-form';
import $ from 'jquery';
import React from 'react'; 

// 如果不是使用 List.Item 作为 children



class Test extends React.Component {
   state = {
    data: [],
    all:{},
	img:{},
  };

  render() {
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
	      onLeftClick={() => this.props.history.goBack()}  
	    >天气</NavBar>
	    
	    <img src = {this.state.img}  style={{width:"100%"}}/>
	    <div style={{fontSize:'2rem'}}>
		    <p>{this.state.all.weather}</p>
		    <p>{this.state.all.week}</p>
		    <p>{this.state.all.highest}~{this.state.all.lowest}</p>
	    </div>
	    
    </div>);
  }
  componentDidMount(){
		var _this = this;
		$.ajax({
			type:"get",
			url:"http://api.tianapi.com/txapi/tianqi/",
			data:{key:'383e1589f7a14bd7b281a272f684ea34',city:'郑州'},
			dataType:"json",
			success:function(data){
				_this.setState({all:data.newslist[0]})
				console.log(_this.state.all)
			}
				
		});
		//http://api.tianapi.com/meinv/     图片
		$.ajax({
			type:"get",
			url:"http://api.tianapi.com/meinv/",
			data:{key:'383e1589f7a14bd7b281a272f684ea34',num:10},
			dataType:"json",
			success:function(data){
				_this.setState({img:data.newslist[parseInt(Math.random()*10)].picUrl})
				console.log(_this.state.img)
			}
				
		});
	}
}

const Weather = createForm()(Test);
export default Weather;