import { NavBar, Icon,Button, WhiteSpace,  Grid ,List,Toast} from 'antd-mobile';
import React,{Component} from 'react';

const arr = [
	{img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1559182233&di=0f8cf1897ba9c01edf50f84f2b130063&imgtype=jpg&er=1&src=http%3A%2F%2Fpic.qiantucdn.com%2F58pic%2F15%2F16%2F33%2F70T58PICFfN_1024.jpg',text:'订阅',url:'/book'},
	{img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558587513528&di=bec7f23dfe6edd684a64d5bae6a227cc&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F15%2F22%2F06%2F25N58PICAYn_1024.jpg',text:'预约',url:'/order'},
	{img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558587513326&di=dcbd14cd899e8bb3b20c8c6cda9136b4&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F14%2F80%2F65%2F22758PIC9rT_1024.jpg',text:'收藏',url:'/collect'},
	{img:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558587513325&di=1c599f9e17227652ac92c81a9f6df2be&imgtype=0&src=http%3A%2F%2Fpic.58pic.com%2F58pic%2F14%2F72%2F68%2F85e58PICPmV_1024.jpg',text:'历史',url:'/history'}
]
const data = Array.from(arr).map((_val, i) => ({
  icon: _val.img,
	text: _val.text,
	url:_val.url
}))

class Mine extends Component{
	login(){
		this.props.history.push('/login')
	}
	componentWillMount(){
		
//		this.setState({name:this.props.location.state.name})
//		console.log(this.state.name)
	}
	render(){
		return(
			<div>
			    <NavBar
			      mode="light"
			      icon={<Icon type="left" />}
			      onLeftClick={() => this.props.history.push('/home')}
			      rightContent={[
			        <Icon key="1" type="ellipsis" onClick={()=> this.props.history.push('/setting')}/>,
			      ]}
			      
			    >个人中心</NavBar>
			    <Button type="primary" inline style={{ margin: '10% 0 10% 40%',borderRadius:"20px" }} onClick={this.login.bind(this)}>登录</Button>
					<Grid data={data} columnNum={4} onClick={(redata)=>{
						Toast.info("请先登录", 0.5);
						setTimeout(function(){
							window.location.href='#/login'
						},500) 	 
			    }}/>
			    <WhiteSpace size="lg" />
			    <List.Item arrow="horizontal" onClick={() => {
						Toast.info("请先登录", 0.5);
						setTimeout(function(){
							window.location.href='#/login'
						},500) 	 
					}}>商城</List.Item>
			</div>
		)
		
	}
}
export default Mine;