import React,{Component} from 'react'
import Head from '../../components/Head'
import Foot from '../../components/Foot'
import $ from 'jquery';




class Video extends Component{
    constructor(props){
        super(props)
        this.state={
            title:'电视',
            list:[]
        }
    }
	//    () => { window.location.href = item.url }
	turn(a){
		this.props.history.push({pathname:'/videodetail',state:{url:a}})
	}
    render(){
        return(
            <div>
                <Head title={this.state.title}/>
				<div style={{marginTop:'45px',height:'0.1px'}}></div>
                {/* <video src='http://dss1.top/a.mp4' style={{width:'100%'}}></video> */}
                {
                    this.state.list.map((item,i)=>{
                        return(
                            <div key={i} onClick={this.turn.bind(this,item.url)} style={{ position: "relative", cursor:'pointer'}}>
                                <img src={item.imgurl} style={{width:'90%',marginLeft:'5%',marginBottom:'10px'}}/> 
                                <h2 style={{margin:'0',marginLeft:'5%',}}>
                                    {item.title}
                                    
                                    <span style={{float:'right',marginRight:'5%',color:'#333',fontSize:'16px'}}>{item.time}</span>
                                    <span style={{ float: 'right', marginRight: '10%', color: '#333', fontSize: '16px' }}>
                                        播放{item.nums}次
                                    </span>
                                </h2>
                                <p style={{borderBottom:'3px dashed #ccc',margin:'8px 5%',textIndent:'2rem',padding:'0'}}>{item.content}</p>
                            </div>
                        )
                    })
                }
                <Foot />
            </div>
        )
    }
    componentDidMount(){
        var _this = this
        $.ajax({
            type:'get',
            dataType:'json',
            //url:'../../../public/data.json',
            url:'data.json',
            success:function(data){
                //console.log(data.list)
                _this.setState({
                    list:data.list
                })
                console.log(_this.state.list)
            }
        })
    }
}

export default Video;