import React,{Component} from 'react';
import {HashRouter as Router,Route,Switch} from 'react-router-dom'
import {Tabs, WhiteSpace, PullToRefresh, ListView, Button, NavBar, Icon} from 'antd-mobile';
import Action from '../Action';
import Store from '../Stroe';
import $ from 'jquery';
class Markethome extends Component {
    constructor(props){
        super(props)
        this.changeitem=this.changeitem.bind(this)
    }
    state={
        list:Store.getState().list,
        username:Store.getState().username,
        pid:'',
        data:''
    }

    changeitem(){
        this.setState({
            list:Store.getState().list,
            username:Store.getState().username
        })
    }

    componentDidMount(){
        Store.subscribe(this.changeitem)
    }
    addBtn(){
        var num = $('.addNum').val();
        Store.dispatch(Action.tap(num))
        // this.props.history.push({pathname:'/cart',state:{num:num}})
    }
    render(){
        return(
            <div>
                <NavBar
                    mode="light"
                    style={{position:'fixed',zIndex:20,width:'100%',top:0}}
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}

                >商品详情</NavBar>

                <div style={{marginTop:'45px',height:'0.1px'}}></div>

                <img style={{width:'100%'}} src={this.state.data.pimg} alt=""/>
                <h3>{this.state.data.pname}</h3>
                <h3>{this.state.data.pdesc}</h3>
                <input type="text" className="addNum" placeholder='1' style={{width: '40px',
                    height: '40px',
                    textAlign: 'center',
                    marginRight: '20px'}}/>
                <input type="button" value="加入购物车" onClick={this.addBtn.bind(this)} style={{
                    width: '120px',
                    height:'40px',
                    border: 'none',
                    background: 'red',
                    color: 'white',
                    cursor: 'pointer'
                }}/>
            </div>
        )
    }

    componentWillMount(){
        this.setState({
            pid:this.props.location.state.url
        })
    }
    componentDidMount() {
        var _this = this;
        $.ajax({
            url:'http://jx.xuzhixiang.top/ap/api/detail.php',
            type:'get',
            data:{id:this.state.pid},
            dataType:'json',
            success:(data)=>{
                this.setState({
                    data:data.data
                })
            }
        })
    }


}
export default Markethome;