import React,{Component} from 'react'
import '../fonts/iconfont.css';
import foot from './foot.module.css'
// import './foot.module.css'
class Foot extends Component{
   tt1(){
       window.location.href='#/home'
   }
    tt2() {
        window.location.href = '#/video'
    }
    tt3(){
       window.location.href='#/live'
   }
    tt4() {
        window.location.href = '#/server'
    }
    render() {
        return (
            <div className={foot.box}>
                <div className='item' onClick={this.tt1.bind(this)}>
                    <p className='iconfont icon-shouye' style={{margin:0,padding:0,fontSize:'1.6rem'}}></p>
                    <p style={{ margin: 0, padding: 0 }}>首页</p>
                </div>
                <div className='item' onClick={this.tt2.bind(this)}>
                    <p className='iconfont icon-dianshi' style={{ margin: 0, padding: 0 ,fontSize:'1.6rem'}}></p>
                    <p style={{ margin: 0, padding: 0 }}>电视</p>
                </div>
                <div className='item' onClick={this.tt3.bind(this)}>
                    <p className='iconfont icon-zhibo' style={{ margin: 0, padding: 0 ,fontSize:'1.6rem'}}></p>
                    <p style={{ margin: 0, padding: 0 }}>直播</p>
                </div>
                <div className='item' onClick={this.tt4.bind(this)}>
                    <p className='iconfont icon-wode' style={{ margin: 0, padding: 0,fontSize:'1.6rem' }}></p>
                    <p style={{ margin: 0, padding: 0 }}>发现</p>
                </div>
            </div>
        );
    }
}
export default Foot;