import {  WhiteSpace,NavBar, Icon, Button,Card,InputItem} from 'antd-mobile';
import { createForm } from 'rc-form';
import {HashRouter as Router,Route,NavLink} from 'react-router-dom'
import React from 'react' ;
import Constellation from './Constellation' ;

import coll from './Collect.module.css' ;
import $ from 'jquery' 


// 如果不是使用 List.Item 作为 children
class Test extends React.Component {
   state = {
    data: [],
    cols: 1,
    asyncValue: [],
  };
  onChangeColor = (color) => {
    this.setState({
      colorValue: color,
    });
  };
  cha(){
    var xing = $('.xingzuo').val()
    this.props.history.push({ pathname: '/exchange/constellation', state: { id:xing } })
  }

  render() {
    const { getFieldProps } = this.props.form;
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
        onLeftClick={() => this.props.history.push('/server')}  
        style={{position: 'fixed',width:'100%',zIndex:'20',top:0}}
	    >星座</NavBar>
      <div style={{marginTop:'45px'}}></div>
      <WhiteSpace />
      <InputItem
        {...getFieldProps('label8')}
        placeholder="星座名"
        className = "xingzuo"
        labelNumber={5}
      >输入星座</InputItem>
      <WhiteSpace />

      <Button type="primary" style={{width:'80px',margin:'0 auto'}} onClick={this.cha.bind(this)}>查询</Button>
      <WhiteSpace />
      <Router>
        <div>
          <Route path="/exchange/constellation" component={Constellation}/>
        </div>
      </Router>
    </div>);
  }
  /*componentDidMount(){
    var _this = this;
    $.ajax({
      type:'get',
      url:'http://api.tianapi.com/txapi/star/',
      data:{key:'383e1589f7a14bd7b281a272f684ea34',astro:'taurus'},
      dataType:'json',
      success:function (data) {
        console.log(data.newslist)
        _this.setState({
          data:data.newslist
        })
      }
    })
  }*/
}

const Exchange = createForm()(Test);
export default Exchange;