import React from 'react';
import { List, InputItem, NavBar,Icon,Button,Toast} from 'antd-mobile';
import { createForm } from 'rc-form';
import $ from 'jquery'

class BasicInputExample extends React.Component {
  componentDidMount() {
    // this.autoFocusInst.focus();
  }
  handleClick = () => {
    this.inputRef.focus();
  }
  turn(a){
    var _this = this;
    if($('.phone input').val().length == 0||$('.password input').val().length==0){
        Toast.info('没输入账号密码啊', 1);
    }else {
        $.ajax({
            type:'get',
            url:'http://jx.xuzhixiang.top/ap/api/login.php',
            data:{username:$('.phone input').val(),password:$('.password input').val()},
            dataType:'json',
            success:function(data){
                console.log(data)
                if(data.msg === '登录成功'){
                    localStorage.setItem("token", data.data.id)
                    localStorage.setItem("username", data.data.username)
                    Toast.info(data.msg, 1);
                    setTimeout(function(){
                        _this.props.history.push({pathname:'/mine1',state:{id:data.data.id}})
                    },1000)

                }else{
                    Toast.info('账号密码有问题你心里没数吗', 2);
                }
            }
        })
    }
  }
  render() {
    const { getFieldProps } = this.props.form;
    return (
      <div>
        <NavBar
				      mode="light"
				      icon={<Icon type="left" />}
				      onLeftClick={() => this.props.history.goBack()}  
				    >登录</NavBar>

        <List renderHeader={() => '已有账号登录'}>
          <InputItem
            {...getFieldProps('phone')}
            type="phone"
            className='phone'
            placeholder="188 8888 8888"
						ref = 'ipt'
          >手机号码</InputItem>
          <InputItem
            {...getFieldProps('password')}
            type="password"
            className='password'
            placeholder="****"
          >密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码</InputItem>
        </List>
				<List renderHeader={() => '密码4到8位之间'} />
    		<Button type="primary" style={{ margin: '10% 0',borderRadius:"20px" }} onClick={this.turn.bind(this,$('.phone').val)}>登录</Button>
        <p style={{margin:"10px auto",width:'65px'}}>没有账号?</p>
        <a href='#/register' style={{margin:"0 auto",width:'100px',display:'block'}}>手机号快速注册</a>
      </div>
    );
  }
	componentDidMount(){
		$(".password input").attr({'minlength':'4','maxlength':'8'})
	}
}

const Login = createForm()(BasicInputExample);
export default Login;