import React,{Component} from 'react';
import { HashRouter as Router,Route,Switch,Redirect} from 'react-router-dom';    //Redirect重定向
import Server from './views/Server';
import Mine from './views/Mine';
import Mine1 from './views/Mine1';
import History from './views/History';
import Exchange from './views/Exchange';
import Setting from './views/Setting';
import Setting1 from './views/Setting1';
import Market from './redux/market/Market';
import Login from './views/Login';
import Register from './views/Register';
import Weather from './views/Weather';
import Weather1 from './views/Weather1';
import Live from './views/Live';
import Collect from './views/Collect';
import Search from './views/Search';
import Search1 from './views/Search1';
import Tax from './views/Tax';
import Bus from './views/Bus';
import Home from './redux/views/Home';
import Videodetail from './redux/views/Videodetail';
import Video from './redux/views/Video';
import Detail from './redux/views/Detail';
import Markethome from './redux/market/Markethome';


import './App.css';

class App extends Component{
	render(){
		return(
			<div>
				<Router>
					<div>
						<Switch>
							<Route path="/server" component={Server} />
							<Route path="/mine" component={Mine} />
							<Route path="/mine1" component={Mine1} />
							<Route path="/history" component={History} />
							<Route path="/setting" component={Setting} />
							<Route path="/setting1" component={Setting1} />
							<Route path="/market" component={Market} />
							<Route path="/login" component={Login} />
							<Route path="/register" component={Register} />
							<Route path="/weather" component={Weather} />
							<Route path="/weather1" component={Weather1} />
							<Route path="/live" component={Live} />
							<Route path="/collect" component={Collect} />
							<Route path="/exchange" component={Exchange} />
							<Route path="/search" component={Search} />
							<Route path="/search1" component={Search1} />
							<Route path="/tax" component={Tax} />
							<Route path="/bus" component={Bus} />
							<Route path="/bus" component={Bus} />
							<Route path="/home" component={Home} />
							<Route path="/video" component={Video} />
							<Route path="/detail" component={Detail} />
							<Route path="/videodetail" component={Videodetail} />
							<Route path="/markethome" component={Markethome} />

							<Redirect to='/home'/>
						</Switch>	
					</div>
				</Router>
			</div>
		)
	}
}

export default App;
