const data = Array.from(arr).map((_val, i) => ({
  icon: _val.img,
  text: _val.text,
  url:_val.url
}));
class Server extends Component{
	constructor(props){
		super(props)
		this.state={
			title:'服务',
			all:{}
		}
	}
	turn(){
//		this.props.history.push('https://gw.alipayobjects.com/zos/rmsportal/nywPmnTAvTmLusPxHPSu.png')
		//window.location.href='https://gw.alipayobjects.com/zos/rmsportal/nywPmnTAvTmLusPxHPSu.png';
		console.log(this.refs.ipt)
	}
	render(){
		return(
			<div>
				<Head title={this.state.title}/>
	    		<NoticeBar  icon={<Icon type="check-circle-o" size="xxs" />}>
		    		<span>郑州</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
		    		<span >{this.state.all.weather}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
		    		<span>{this.state.all.highest}</span>~<span>{this.state.all.lowest}</span>
			    </NoticeBar>
			    <Grid data={data} columnNum={3} hasLine={false}  ref='ipt' onClick={(redata)=>{
			    	this.props.history.push({pathname:redata.url})
			    	 
			    }}/>
			    <Foot/>
		  	</div>
		)
	}
	componentDidMount(){
		var _this = this;
		$.ajax({
			type:"get",
			url:"http://api.tianapi.com/txapi/tianqi/",
			data:{key:'383e1589f7a14bd7b281a272f684ea34',city:'郑州'},
			dataType:"json",
			success:function(data){
				_this.setState({all:data.newslist[0]})
			}
				
		});
	}
}



export default Server;