import { NavBar, Icon,Carousel, Card, WingBlank, WhiteSpace} from 'antd-mobile/lib/index';
import React,{Component} from 'react';
import coll from '../../views/Collect.module.css' ;

import $ from 'jquery';

class Market extends Component{
	state={
		list:[],
		banner:[],
		data: ['1', '2', '3'],
		imgHeight: 176,
	}
	mai(a){
		this.props.history.push({pathname:'/markethome',state:{url:a}})
	}
	render(){
		return(
			<div>
			    <NavBar
						mode="light"
						style={{position:'fixed',zIndex:20,width:'100%',top:0}}
			      icon={<Icon type="left" />}
			      onLeftClick={() => this.props.history.goBack()}

			    >商城</NavBar>

				<div style={{marginTop:'45px',height:'0.1px'}}></div>
				{/*<iframe src='http://go.qingk.cn/' style={{width:'100%',height:'736px',border:'0',marginTop:'45px'}}></iframe>*/}

				<div >
					<WingBlank>
						<Carousel
							autoplay={false}
							infinite
						>
							{this.state.data.map((item,i) => (
								<a
									key={i}
									href='#'
									style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
								>
									<img
										src={item.banner_img_url}
										alt=""
										style={{ width: '100%', verticalAlign: 'top' }}
										onLoad={() => {
											// fire window resize event to change height
											window.dispatchEvent(new Event('resize'));
											this.setState({ imgHeight: 'auto' });
										}}
									/>
								</a>
							))}
						</Carousel>
					</WingBlank>
				</div>

				{
					this.state.list.map((item,i)=>{
						return(
							<div key={i} onClick={this.mai.bind(this,item.pid)}>
								<WingBlank size="lg">
									<WhiteSpace size="lg" />
									<Card>
										<Card.Header
											className={coll.card}
											title={item.pprice}
											thumb={item.pimg}
											extra={item.pid}
										/>
										<Card.Body>
											<div>{item.pdesc}</div>
										</Card.Body>
										<Card.Footer content={item.pname} extra={<div>商品和平台无关</div>} />
									</Card>
									<WhiteSpace size="lg" />
								</WingBlank>
							</div>
						)
					})
				}
			</div>
		)
		
	}
	componentWillMount() {
		var _this = this;
		$.ajax({
			url:'http://jx.xuzhixiang.top/ap/api/productlist.php',
			type:"get",
			data:{uid:'6295'},
			dataType:'json',
			success:function (data) {
				_this.setState({
					list:data.data
				})
			}
		});
		$.ajax({
			url:' http://jx.xuzhixiang.top/ap/api/bannerlist.php',
			type:"get",
			data:{uid:'6295'},
			dataType:'json',
			success:function (data) {
				_this.setState({
					banner:data.data
				})
			}
		});
	}

	componentDidMount() {
		// simulate img loading
		$.ajax({
			url:'http://jx.xuzhixiang.top/ap/api/bannerlist.php',
			type:"get",
			data:{uid:'6295'},
			dataType:'json',
			success: (data)=> {
				setTimeout(() => {
					this.setState({
						data: data.data,
					});
				}, 100);
			}
		});
	}
}
export default Market;