
import React,{Component} from 'react';
import Head from '../components/Head';
import Foot from '../components/Foot';
import { Player, ControlBar, ReplayControl,
  ForwardControl, CurrentTimeDisplay,
  TimeDivider, PlaybackRateMenuButton, VolumeMenuButton } from 'video-react';
import live from './Live.module.css';

class Live extends Component{
	login(){
		this.props.history.push('/login')
	}
	componentWillMount(){
		
//		this.setState({name:this.props.location.state.name})
//		console.log(this.state.name)
    }
    constructor(props){
		super(props)
		this.state={
			title:'直播'
		}
    }
    //<canvas id="myCanvas" style={{border:'1px solid #ddd',display:'block',margin:'20px auto'}}></canvas>
	render(){
		return(
			<div>
			    <Head title={this.state.title}/>
                {/*<video src='//f.us.sinaimg.cn/000YlC6Flx07u4zuVfa001041201JWCd0E010.mp4?label=mp4_720p&template=1280x720.23.0&Expires=1558587848&ssig=3aE80WC0I%2B&KID=unistore,video' style={{width:'100%'}}></video>
                <video src="//locallimit.us.sinaimg.cn/001sNc6blx07u4BXpHuE01041200lAWw0E010.mp4?label=mp4_720p&amp;template=1280x720.23.0&amp;Expires=1558588086&amp;ssig=y4qmynVA4Q&amp;KID=unistore,video" style={{width:'100%'}}></video>		
				<Player
				  className={live.play}
				  playsInline
				  poster="/assets/poster.png"
				  src="https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
				/>*/}
				<Player  className={live.play}>
                <source src="http://peach.themazzone.com/durian/movies/sintel-1024-surround.mp4" />
                <source src="http://mirrorblender.top-ix.org/movies/sintel-1024-surround.mp4" />
                <ControlBar>
                  <ReplayControl seconds={10} order={1.1} />
                  <ForwardControl seconds={30} order={1.2} />
                  <CurrentTimeDisplay order={4.1} />
                  <TimeDivider order={4.2} />
                  <PlaybackRateMenuButton
                    rates={[5, 2, 1, 0.5, 0.1]}
                    order={7.1}
                  />
                  <VolumeMenuButton disabled />
                </ControlBar>
              </Player>
                <Foot/>
			</div>
		)
		
	}
}
export default Live;