import {  WhiteSpace,NavBar, Icon, WingBlank,Card} from 'antd-mobile';
import { createForm } from 'rc-form';
import React from 'react' ;
import coll from './Collect.module.css' ;
import $ from 'jquery' 


// 如果不是使用 List.Item 作为 children
class Test extends React.Component {
   state = {
    data: [],
    cols: 1,
    asyncValue: [],
  };
  onChangeColor = (color) => {
    this.setState({
      colorValue: color,
    });
  };


  render() {
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
        onLeftClick={() => this.props.history.goBack()}  
        style={{position: 'fixed',width:'100%',zIndex:'20',top:0}}
	    >历史记录</NavBar>
      <div style={{marginTop:'45px'}}></div>
      {
        this.state.data.map((item,i)=>{
          return(
            <div key={i}>
              <WhiteSpace size='lg'/>
              <Card>
                <Card.Header
                  className={coll.card}
                  title={item.ctime}
                  thumb={item.picUrl}
                  extra={item.description}
                />
                <Card.Body>
                  <div>{item.title}</div>
                </Card.Body>
                <Card.Footer content={item.description} extra={<div>{item.ctime}</div>} />
              </Card>
            </div>
          )
        })
      }
    </div>);
  }
  componentDidMount(){
    var _this = this;
    $.ajax({
      type:'get',
      url:'http://api.tianapi.com/keji/',
      data:{key:'383e1589f7a14bd7b281a272f684ea34',num:'7'},
      dataType:'json',
      success:function (data) {
        _this.setState({
          data:data.newslist
        })
      }
    })
  }
}

const History = createForm()(Test);
export default History;