import {NavBar, Icon} from 'antd-mobile';
import { createForm } from 'rc-form';
import React from 'react'; 

// 如果不是使用 List.Item 作为 children



class Test extends React.Component {
	constructor(props){
		super(props)
		this.state={
			url:''
		}
	}
  render() {
    const { getFieldProps } = this.props.form;
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
				onLeftClick={() => this.props.history.goBack()}  
				style={{position: 'fixed',width:'100%',marginBottom:'45px'}}
	    >视频详情</NavBar>
	    <iframe src={this.state.url} style={{width:"100%" ,height:"736px",border:'none'}}>
	    
	    </iframe>
    </div>);
  }
	componentDidMount(){
		this.setState({
			url:this.props.location.state.url
		})
	}
}


const Videodetail = createForm()(Test);
export default Videodetail;