import React from 'react' ;
import $ from 'jquery' ;
import { SearchBar,} from 'antd-mobile';

class Search extends React.Component {
  state = {
    allList:[]
  }
  componentDidMount() {
    this.autoFocusInst.focus();
  }
  onChange= (value) => {
    this.setState({ value });
  };
  clear = () => {
    this.setState({ value: '' });
    this.props.history.goBack();
  };
  submit(){
    var con = $('.heihei input').val();
    $.ajax({
      url:`http://suggestion.baidu.com/su?wd=${con}&cb=window.baidu.sug`,
      callback:'jsonp',
      dataType: 'json',
      success:(data)=>{
        console.log(data)
      }
    })
  }
  render() {
    return (<div>
      <SearchBar className='heihei' placeholder="搜索你想要的" ref={ref => this.autoFocusInst = ref} onCancel={()=>this.props.history.goBack()}
      onSubmit={this.submit.bind(this)}
      />
      <input type="text" ref='ipt'/>
      <ul className='ul'>
        {
          this.state.allList.map((item,i)=>{
            return(
                <li key={i}>
                    <div onClick={()=> this.props.history.push('/detail')}>{item.title}</div>
                </li>
            )
          })
        }
      </ul>
      
    </div>);
  }
	// $('.heihei input').oninput = function(){
	// 		var oScript = document.createElement("script");
	// 		oScript.src = "https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su?wd="+$('.heihei input').val()+"&cb=bb"
	// 		document.body.appendChild(oScript);
	// 	}
	// function bb(data){
	// 		data = data.s;
	// 		var str = "";
	// 		for(var i = 0; i < data.length;i++){
	// 			str += `<li><a href="https://www.baidu.com/s?wd=${data[i]}">${data[i]}</a></li>`
	// 		}
	// 		$(".ul").innerHTML = str;
	// 	}
    //页面加载前，将数据获取
  componentWillMount() {
    var _this = this;
    $.ajax({
      url:'http://api.tianapi.com/generalnews/',
      data:{key:'383e1589f7a14bd7b281a272f684ea34',
        num:'999'},
      dataType:'json',
      type:'get',
      success: (data)=> {
        _this.setState({
          allList:data.newslist
        })
      }
    })
  }

}
export default Search;