import {  NavBar, Icon } from 'antd-mobile';
import { createForm } from 'rc-form';
import React from 'react'; 

// 如果不是使用 List.Item 作为 children



class Test extends React.Component {
   state = {
    data: [],
    all:{},
	img:{},
  };

  render() {
    return (<div>
    	<NavBar
	      mode="light"
	      icon={<Icon type="left" />}
				onLeftClick={() => this.props.history.goBack()}  
				style={{position: 'fixed',width:'100%'}}
	    >个税</NavBar>
	    <iframe src='https://m.rong360.com/calculator/wuxianyijin.html' style={{width:"100%" ,height:"736px",border:'none'}}>
	    
	    </iframe>
	    
    </div>);
  }

}

const Tax = createForm()(Test);
export default Tax;