import React,{Component} from 'react'
import '../fonts/iconfont.css'
import { NavBar, Icon } from 'antd-mobile';

class Head extends Component{
    search(){
        window.location.href='#/search'
    }
    mine(){
        if(localStorage.getItem("token")){
            window.location.href = '#/mine1'
        }else{
            window.location.href = '#/mine'
        }
        
    }
    render(){
        return(
            <div>

                <NavBar
			      mode="dark"
			      leftContent={[
			        <Icon key="1" type='check-circle-o' onClick={this.mine.bind(this)}/>,
			      ]}
			      onLeftClick={() => this.mine.bind(this)}
			      rightContent={[
			        <Icon key="1" type="search" onClick={this.search.bind(this)}/>,
			      ]}
				  style={{position:'fixed',zIndex:'20',top:'0',width:'100%'}}
			      
			    >{this.props.title}</NavBar>
            </div>
        )
    }
}


export default Head;