import React,{Component} from 'react';
import Head from '../../components/Head';
import Foot from '../../components/Foot';
import {HashRouter as Router,Route,Switch} from 'react-router-dom'
import { Tabs, WhiteSpace,PullToRefresh, ListView,Button } from 'antd-mobile';
import $ from 'jquery';
import Detail from './Detail'
import Action from '../Action';
import Stroe from '../Stroe';
class Home extends Component{
    constructor(props){
        super()
        this.state={
            list1:[],
            list2:[],
            title:'桔子新闻'
           // str: Stroe.getState()
        }
       // this.changeItem = this.changeItem.bind(this) 
    }
    // changeItem() {
    //     this.setState({
    //         str: Stroe.getState()
    //     })  
    //     console.log(Stroe.getState())
    // }

    //推荐的新闻展示
    //新闻全文展示接口http://api.tianapi.com/htmltext/	
    /*tap(msg){
       // window.location.href='#/detail'
      //console.log(msg)
     Stroe.dispatch(Action.tap(msg))
     window.location.href = '#/detail'
    }*/

    renderContent = tab =>
        (
            
        <div style={{margin:' 0 0 50px 0'}}>
            {
                this.state.list1.map((item,i)=>{
                    return(
                        
                        <div key={i} >
                            <Router>
                                <a href = {item.url} style={{color:'#333'}} key={i}>
                                    <div style={{width:'90%',margin:'0 auto'}} >
                                    <h3>{item.title}</h3>
                                    <div>
                                        <img src={item.picUrl} alt='图片' style={{width:'30%',height:'80px'}}/>
                                        <img src={item.picUrl} alt='图片' style={{width:'30%',height:'80px',margin:'0 5px'}}/>
                                        <img src={item.picUrl} alt='图片' style={{width:'30%',height:'80px'}}/>
                                    </div>
                                    <span>{item.description}</span>&nbsp;&nbsp;&nbsp;
                                    <span>{item.ctime}</span>
                                    <Switch>
                                    <Route path='/home/detail' component={Detail} />
                                    </Switch>       
                                    </div>
                               </a>
                            </Router>
                        </div>
                        
                    )
                })
            }
        </div>       
        );
        componentDidMount(){
            var _this = this;
           // Stroe.subscribe(this.changeItem)
           $.ajax({
                type: "get",
                url: "http://api.tianapi.com/world/",
                data: {
                    key:'4740d8523124e3212da5120021cb0a3c',
                    num:10,
                    page:3
                },
                dataType: "json",
                success: function (data) {
                    //console.log(data.newslist)
                    _this.setState({
                        list1:data.newslist
                        
                    })
                }
            }); 
            //要闻 接口请求
            $.ajax({
                type: "get",
                url: "http://api.tianapi.com/guonei/",
                data: {
                    key: '4740d8523124e3212da5120021cb0a3c',
                    num: 10,
                    page: 3
                },
                dataType: "json",
                success: function (data) {
                   // console.log(data.newslist)
                    _this.setState({
                        list2: data.newslist
                    })
                   // console.log(_this.state.list2)
                }
            });
        }

    renderContent1 = tab =>
        (
            <div style={{ margin: ' 0 0 50px 0' ,background:'#fff'}}>
                {
                    this.state.list2.map(function (item, i) {
                        return (
                            <a href={item.url} style={{color:'#333'}} key={'a'+i}>
                            <div style={{ width: '90%', margin: '0 auto', display: "flex", justifyContent: " space-between", marginBottom: '10px', background: '#eee'}}>
                                <div style={{width:'60%'}}>
                                    <h3>{item.title}</h3> 
                                    <span>{item.description}</span>&nbsp;&nbsp;&nbsp;
                                    <span>{item.ctime}</span> 
                                </div> 
                                <img src={item.picUrl} alt='图片' style={{ width: '30%', height: '80px' }} />                               
                            </div>
                            </a>
                        )
                    })
                }
            </div>
        );
        
    

    render() {
        const tabs = [
        { title: '推荐' },
        { title: '要闻' },
        { title: '时政' },
        { title: '社会' },
        { title: '视频' },
        { title: '文化|旅游' },
        { title: '健康' },
        { title: '教育' },
        { title: '音频' },
    ];
        return(
            <div>
                <Head title={this.state.title}/>
				<div style={{marginTop:'45px',height:'0.1px'}}></div>
				
                <WhiteSpace />
                <Tabs tabs={tabs} renderTabBar={props => <Tabs.DefaultTabBar {...props} page={5} />}>
                    {this.renderContent}
                    {this.renderContent1}
                </Tabs>
                <WhiteSpace />

                <Foot />
            </div>
        )
    }
}


export default Home;