const Reducer = (state="", action) => {
    switch (action.type) {
        case 'TAP':
            let str1 = state;
            str1=action.text;
           // console.log(state,str1)
            return str1 
        // eslint-disable-next-line no-fallthrough
        default:
            return state;
    }
}


export default Reducer